"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import DashboardTitle from "@/components/dashboard/DashboardTitle";
import { Card } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Button } from "@/components/ui/button";
import { toast } from "react-hot-toast";
import { useCredentials } from "@/hooks/useCredentials";
import { Skeleton } from "@/components/ui/skeleton";

interface IAMRole {
  RoleName: string;
  RoleId: string;
  Arn: string;
  CreateDate: string;
  Description?: string;
  AssumeRolePolicyDocument?: string;
}

interface IAMPolicy {
  PolicyName: string;
  PolicyId: string;
  Arn: string;
  CreateDate: string;
  Description?: string;
  IsAttachable?: boolean;
  AttachmentCount?: number;
  DefaultVersionId?: string;
}

interface IAMGroup {
  GroupName: string;
  GroupId: string;
  Arn: string;
  CreateDate: string;
}

export default function IAMAdvancedPage() {
  const { credentials, loading: credentialsLoading } = useCredentials();
  const [selectedCredential, setSelectedCredential] = useState<string>("");
  const [activeTab, setActiveTab] = useState<string>("roles");
  
  const [roles, setRoles] = useState<IAMRole[]>([]);
  const [groups, setGroups] = useState<IAMGroup[]>([]);
  const [policies, setPolicies] = useState<IAMPolicy[]>([]);
  
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    if (credentials && credentials.length > 0 && !selectedCredential) {
      setSelectedCredential(credentials[0].id);
    }
  }, [credentials, selectedCredential]);

  useEffect(() => {
    if (selectedCredential && activeTab) {
      fetchData();
    }
  }, [selectedCredential, activeTab]);

  const fetchData = async () => {
    if (!selectedCredential) return;

    setLoading(true);
    setError("");

    try {
      let endpoint = "";
      
      switch (activeTab) {
        case "roles":
          endpoint = `/api/aws/iam-advanced/roles?credentialId=${selectedCredential}`;
          break;
        case "groups":
          endpoint = `/api/aws/iam-advanced/groups?credentialId=${selectedCredential}`;
          break;
        case "policies":
          endpoint = `/api/aws/iam-advanced/policies?credentialId=${selectedCredential}`;
          break;
        default:
          throw new Error("Tab inválida");
      }
      
      const response = await fetch(endpoint);
      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || `Falha ao obter dados do IAM para ${activeTab}`);
      }

      switch (activeTab) {
        case "roles":
          setRoles(data.roles || []);
          break;
        case "groups":
          setGroups(data.groups || []);
          break;
        case "policies":
          setPolicies(data.policies || []);
          break;
      }
    } catch (err: any) {
      console.error(`Erro ao buscar dados do IAM para ${activeTab}:`, err);
      setError(err.message || `Ocorreu um erro ao buscar os dados do IAM para ${activeTab}`);
      toast.error(`Erro ao carregar dados do IAM para ${activeTab}`);
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString("pt-BR", {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit"
    });
  };

  // Renderiza a tabela de perfis (roles)
  const renderRolesTable = () => {
    if (loading) {
      return (
        <div className="space-y-4">
          {[1, 2, 3].map((i) => (
            <div key={i} className="flex flex-col gap-2">
              <Skeleton className="h-8 w-full" />
              <Skeleton className="h-16 w-full" />
            </div>
          ))}
        </div>
      );
    }

    if (roles.length === 0) {
      return (
        <div className="text-center py-8 text-gray-500">
          Nenhum perfil IAM encontrado. Crie um novo perfil ou selecione outra credencial.
        </div>
      );
    }

    return (
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Nome do Perfil
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                ID
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Data de Criação
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Descrição
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Ações
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {roles.map((role) => (
              <tr key={role.RoleId} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm font-medium text-gray-900">{role.RoleName}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-500 truncate max-w-xs">{role.RoleId}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {formatDate(role.CreateDate)}
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-900">{role.Description || "Sem descrição"}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  <div className="flex space-x-2">
                    <button
                      onClick={() => {
                        toast.error("Funcionalidade não implementada");
                      }}
                      className="text-indigo-600 hover:text-indigo-900"
                    >
                      Ver Políticas
                    </button>
                    <button
                      onClick={() => {
                        toast.error("Funcionalidade não implementada");
                      }}
                      className="text-red-600 hover:text-red-900"
                    >
                      Excluir
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  // Renderiza a tabela de grupos
  const renderGroupsTable = () => {
    if (loading) {
      return (
        <div className="space-y-4">
          {[1, 2, 3].map((i) => (
            <div key={i} className="flex flex-col gap-2">
              <Skeleton className="h-8 w-full" />
              <Skeleton className="h-16 w-full" />
            </div>
          ))}
        </div>
      );
    }

    if (groups.length === 0) {
      return (
        <div className="text-center py-8 text-gray-500">
          Nenhum grupo IAM encontrado. Crie um novo grupo ou selecione outra credencial.
        </div>
      );
    }

    return (
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Nome do Grupo
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                ID
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Data de Criação
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Ações
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {groups.map((group) => (
              <tr key={group.GroupId} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm font-medium text-gray-900">{group.GroupName}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-500 truncate max-w-xs">{group.GroupId}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {formatDate(group.CreateDate)}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  <div className="flex space-x-2">
                    <button
                      onClick={() => {
                        toast.error("Funcionalidade não implementada");
                      }}
                      className="text-indigo-600 hover:text-indigo-900"
                    >
                      Ver Usuários
                    </button>
                    <button
                      onClick={() => {
                        toast.error("Funcionalidade não implementada");
                      }}
                      className="text-blue-600 hover:text-blue-900"
                    >
                      Ver Políticas
                    </button>
                    <button
                      onClick={() => {
                        toast.error("Funcionalidade não implementada");
                      }}
                      className="text-red-600 hover:text-red-900"
                    >
                      Excluir
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  // Renderiza a tabela de políticas
  const renderPoliciesTable = () => {
    if (loading) {
      return (
        <div className="space-y-4">
          {[1, 2, 3].map((i) => (
            <div key={i} className="flex flex-col gap-2">
              <Skeleton className="h-8 w-full" />
              <Skeleton className="h-16 w-full" />
            </div>
          ))}
        </div>
      );
    }

    if (policies.length === 0) {
      return (
        <div className="text-center py-8 text-gray-500">
          Nenhuma política IAM encontrada. Crie uma nova política ou selecione outra credencial.
        </div>
      );
    }

    return (
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Nome da Política
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                ID
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Data de Criação
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Anexada
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Contagem de Anexos
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Ações
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {policies.map((policy) => (
              <tr key={policy.PolicyId} className="hover:bg-gray-50">
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm font-medium text-gray-900">{policy.PolicyName}</div>
                  <div className="text-xs text-gray-500">{policy.Description || "Sem descrição"}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-500 truncate max-w-xs">{policy.PolicyId}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {formatDate(policy.CreateDate)}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {policy.IsAttachable ? "Sim" : "Não"}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {policy.AttachmentCount || 0}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  <div className="flex space-x-2">
                    <button
                      onClick={() => {
                        toast.error("Funcionalidade não implementada");
                      }}
                      className="text-indigo-600 hover:text-indigo-900"
                    >
                      Ver Documento
                    </button>
                    <button
                      onClick={() => {
                        toast.error("Funcionalidade não implementada");
                      }}
                      className="text-red-600 hover:text-red-900"
                    >
                      Excluir
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  return (
    <div className="container mx-auto px-4 py-6">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <DashboardTitle
          title="IAM Avançado"
          description="Gerencie perfis, grupos e políticas do AWS Identity and Access Management"
        />

        <div className="my-6">
          <Card className="p-6">
            <div className="flex flex-col md:flex-row justify-between mb-6 gap-4">
              <div className="flex flex-col md:flex-row gap-4 items-center">
                <select
                  value={selectedCredential}
                  onChange={(e) => setSelectedCredential(e.target.value)}
                  disabled={credentialsLoading}
                  className="px-3 py-2 rounded-md border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                >
                  {credentialsLoading ? (
                    <option>Carregando credenciais...</option>
                  ) : (
                    credentials.map((cred) => (
                      <option key={cred.id} value={cred.id}>
                        {cred.name} ({cred.accountId})
                      </option>
                    ))
                  )}
                </select>

                <Button
                  onClick={fetchData}
                  disabled={loading || !selectedCredential}
                  className="bg-indigo-600 hover:bg-indigo-700 text-white"
                >
                  {loading ? "Carregando..." : "Atualizar"}
                </Button>
              </div>

              <Button
                onClick={() => {
                  toast.error(`Funcionalidade de criar ${
                    activeTab === "roles" ? "perfil" : 
                    activeTab === "groups" ? "grupo" : "política"
                  } ainda não implementada`);
                }}
                className="bg-green-600 hover:bg-green-700 text-white"
              >
                Criar {activeTab === "roles" ? "Perfil" : activeTab === "groups" ? "Grupo" : "Política"}
              </Button>
            </div>

            {error && (
              <div className="bg-red-100 text-red-800 p-4 mb-6 rounded-md">
                {error}
              </div>
            )}

            <Tabs 
              defaultValue="roles" 
              value={activeTab}
              onValueChange={(value) => setActiveTab(value)}
              className="mt-6"
            >
              <TabsList className="grid grid-cols-3 mb-8">
                <TabsTrigger value="roles">Perfis (Roles)</TabsTrigger>
                <TabsTrigger value="groups">Grupos</TabsTrigger>
                <TabsTrigger value="policies">Políticas</TabsTrigger>
              </TabsList>
              
              <TabsContent value="roles" className="mt-6">
                {renderRolesTable()}
              </TabsContent>
              
              <TabsContent value="groups" className="mt-6">
                {renderGroupsTable()}
              </TabsContent>
              
              <TabsContent value="policies" className="mt-6">
                {renderPoliciesTable()}
              </TabsContent>
            </Tabs>
          </Card>
        </div>

        <div className="mt-8">
          <Card className="p-6">
            <h3 className="text-lg font-medium mb-4">Gerenciamento IAM Avançado</h3>
            <div className="space-y-4 text-sm text-gray-600">
              <p>
                <strong>AWS IAM</strong> (Identity and Access Management) permite controlar o acesso aos serviços e recursos AWS de forma segura.
              </p>
              <ul className="list-disc pl-5 space-y-2">
                <li><strong>Perfis (Roles):</strong> Identidades que podem ser assumidas por usuários, serviços ou aplicações. São úteis para delegar acesso temporário.</li>
                <li><strong>Grupos:</strong> Coleções de usuários IAM. Você pode atribuir permissões a um grupo para simplificar o gerenciamento.</li>
                <li><strong>Políticas:</strong> Documentos que definem permissões. Podem ser anexadas a identidades (usuários, grupos, perfis) ou recursos.</li>
              </ul>
              <p className="text-yellow-600">
                <strong>Melhores práticas:</strong> Siga o princípio do menor privilégio, concedendo apenas as permissões necessárias. Utilize políticas gerenciadas quando possível e configure MFA para maior segurança.
              </p>
            </div>
          </Card>
        </div>
      </motion.div>
    </div>
  );
} 